import {Pipe, PipeTransform } from '@angular/core';

@Pipe({name: 'formaPagamento', pure: true})
export class FormaPagamentoPipe implements PipeTransform {
    transform(id: number): string {
        return this.getForma(id);
    }
    private getForma(id: number): string {
        if (id === 1) {
            return 'Carto de crdito';
        } else if (id === 2) {
            return 'Boleto';
        } else if (id === 3) {
            return 'Dbito online (TEF)';
        } else if (id === 4) {
            return 'Saldo PagSeguro';
        } else if (id === 5) {
            return 'Oi Paggo';
        } else if (id === 7) {
            return 'Depsito em conta';
        } else {
            return 'No informado';
        }
    }
    /*
    Cdigo de acordo com o PagSeguro:
    Significado
    1 - Carto de crdito: O comprador pagou pela transao com um carto de crdito.
    Neste caso, o pagamento  processado imediatamente ou no mximo em algumas horas, dependendo da sua classificao de risco.
    2 - Boleto: O comprador optou por pagar com um boleto bancrio. Ele ter que imprimir
    o boleto e pag-lo na rede bancria. Este tipo de pagamento  confirmado em geral de um a dois
    dias aps o pagamento do boleto. O prazo de vencimento do boleto  de 3 dias.
    3 - Dbito online (TEF): O comprador optou por pagar com dbito online de algum dos bancos
    com os quais o PagSeguro est integrado. O PagSeguro ir abrir uma nova janela com o Internet
    Banking do banco escolhido, onde o comprador ir efetuar o pagamento. Este tipo de pagamento
     confirmado normalmente em algumas horas.
    4 - Saldo PagSeguro: O comprador possua saldo suficiente na sua conta PagSeguro e pagou integralmente
    pela transao usando seu saldo.
    5 - Oi Paggo *: o comprador paga a transao atravs de seu celular Oi. A confirmao do
    pagamento acontece em at duas horas.
    7 - Depsito em conta: o comprador optou por fazer um depsito na conta corrente do PagSeguro.
    Ele precisar ir at uma agncia bancria, fazer o depsito, guardar o comprovante e retornar
    ao PagSeguro para informar os dados do pagamento. A transao ser confirmada somente aps a
    finalizao deste processo, que pode levar de 2 a 13 dias teis.
    * Os tipos marcados no esto disponveis para utilizao.
    */
}





