import {Pipe, PipeTransform } from '@angular/core';

@Pipe({name: 'statusPagamento', pure: true})
export class StatusPagamentoPipe implements PipeTransform {
    transform(id: number): string {
        return this.getTipoStatus(id);
    }
    private getTipoStatus(id: number): string {
        if (id === 0) {
            return 'Isento';
        } else if (id === 1) {
            return 'Aguardando pagamento';
        } else if (id === 2) {
            return 'Em anlise';
        } else if (id === 3) {
            return 'Paga';
        } else if (id === 4) {
            return 'Disponvel';
        } else if (id === 5) {
            return 'Em disputa';
        } else if (id === 6) {
            return 'Devolvida';
        } else if (id === 7) {
            return 'Cancelada';
        } else if (id === 8) {
            return 'Debitado';
        } else if (id === 9) {
            return 'Reteno temporria';
        } else {
            return 'No localizado';
        }
    }
    /*
    0 - Isento
    Cdigos de acordo com o PagSeguro:
    Cdigo - Significado
    1 - Aguardando pagamento: o comprador iniciou a transao, mas at o momento o
        PagSeguro no recebeu nenhuma informao sobre o pagamento.
    2 - Em anlise: o comprador optou por pagar com um carto de crdito e o PagSeguro
        est analisando o risco da transao.
    3 - Paga: a transao foi paga pelo comprador e o PagSeguro j recebeu uma confirmao
        da instituio financeira responsvel pelo processamento.
    4 - Disponvel: a transao foi paga e chegou ao final de seu prazo de liberao sem ter
        sido retornada e sem que haja nenhuma disputa aberta.
    5 - Em disputa: o comprador, dentro do prazo de liberao da transao, abriu uma disputa.
    6 - Devolvida: o valor da transao foi devolvido para o comprador.
    7 - Cancelada: a transao foi cancelada sem ter sido finalizada.
    8 - Debitado: o valor da transao foi devolvido para o comprador.
    9 - Reteno temporria: o comprador contestou o pagamento junto  operadora do carto de crdito
        ou abriu uma demanda judicial ou administrativa (Procon).
   */
}



